import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPrintCash = (
  row1name = "",
  row1isi = "",
  username = "",
  tanggal = "",
  validby = "",
  title = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF();
  let table = JSON.parse(localStorage.getItem("LaporanCash")) || [];
  var tableRows = [];
  var columTable = [
    "Tanggal",
    "Nama Toko",
    "Kategori",
    "No Bon",
    "Deskripsi",
    "Debet",
    "Kredit",
  ];
  table.forEach((ticket, i) => {
    ticket.alatBayarTunai.forEach((barang, i) => {
      const barangData = [
        ticket.tanggal,
        ticket.namaToko ? ticket.namaToko : "",
        ticket.tipeFaktur,
        ticket.noBon,
        ticket.keterangan,
        parseFloat(barang.tunaiRp).toLocaleString("id-ID"),
        parseFloat(barang.tunaiRp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(barangData);
    });
  });
  doc.text(title, 14, 15);
  doc.setFontSize(10);
  //row 1
  doc.text(row1name, 14, 25);
  doc.text(":", 37, 25);
  doc.text(row1isi, 40, 25);
  // startY is basically margin-top
  doc.autoTable(columTable, tableRows, {
    startY: 40,
    columnStyles: {
      3: {
        columnWidth: 25,
      },
      2: {
        columnWidth: 25,
      },
    },
    theme: "plain",
  });
  let finalY = doc.lastAutoTable.finalY + 10;

  //Stamp
  doc.text("User", 160, finalY + 10);
  doc.text(":", 168, finalY + 10);
  doc.text(username, 170, finalY + 10);
  doc.text("Cetak", 160, finalY + 15);
  doc.text(":", 168, finalY + 15);
  doc.text(tanggal, 170, finalY + 15);
  doc.text("Valid", 160, finalY + 20);
  doc.text(":", 168, finalY + 20);
  doc.text(validby, 170, finalY + 20);
  // doc.save(`${title}_${dateStr}.pdf`);
  var string = doc.output("datauristring");
  var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(embed);
  x.document.close();
};

export default LaporanPrintCash;
