import Axios from "axios";
import React, { Component } from "react";
import { gagal } from "../Alert";
import FormKeuangan from "./Component/FormKeuangan";
import LaporanPrintCash from "./Component/LaporanPrintCash";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class laporanKeuangan extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleCash(data) {
    Axios.get(serverNonMaster + "fico/dc/fico/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem("LaporanCash", JSON.stringify(res.data))
      )
      .catch((err) => gagal("Gagal Mengambil data, Mohon periksa koneksi anda"))
      .then(() =>
        LaporanPrintCash(
          "Tanggal",
          `${data.tglFrom} - ${data.tglTo}`,
          "ADMIN",
          "2020-12-16",
          "ADMIN",
          "LAPORAN TRANSAKSI CASH"
        )
      );
  }
  handleTransfer(data) {
    Axios.get(serverNonMaster + "fico/dc/fico/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem("LaporanCash", JSON.stringify(res.data))
      )
      .catch((err) => gagal("Gagal Mengambil data, Mohon periksa koneksi anda"))
      .then(() =>
        LaporanPrintCash(
          "Tanggal",
          `${data.tglFrom} - ${data.tglTo}`,
          "ADMIN",
          "2020-12-16",
          "ADMIN",
          "LAPORAN TRANSAKSI TRANSFER"
        )
      );
  }
  handleOutstand(data) {}
  handleLainLain(data) {}
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                    <li className="nav-item">
                      <a
                        href="#cash-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link active"
                      >
                        <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Cash</span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#transfer-tab"
                        data-toggle="tab"
                        aria-expanded="true"
                        className="nav-link "
                      >
                        <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Transfer</span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#history-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link"
                      >
                        <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">
                          Outstand Trasfer
                        </span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#lain-lain-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link"
                      >
                        <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">
                          Pendapatan Lain-Lain
                        </span>
                      </a>
                    </li>
                  </ul>
                  <div className="tab-content">
                    <div className="tab-pane show active" id="cash-tab">
                      <FormKeuangan
                        from="CASH"
                        onSubmit={(data) => this.handleCash(data)}
                      />
                    </div>
                    <div className="tab-pane " id="transfer-tab">
                      <FormKeuangan
                        from="TRANSFER"
                        onSubmit={(data) => this.handleTransfer(data)}
                      />
                    </div>

                    <div className="tab-pane" id="history-tab">
                      <FormKeuangan
                        from="OUTSTAND TRANSFER"
                        onSubmit={(data) => this.handleOutstand(data)}
                      />
                    </div>
                    <div className="tab-pane" id="lain-lain-tab">
                      <FormKeuangan
                        from="LAIN-LAIN"
                        onSubmit={(data) => this.handleLainLain(data)}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default laporanKeuangan;
